#ManifestationAssociative-Container{
    background-color: white;

    height: 100vh;
    width: 100vw;
}

#PLP-SurCampus, #PLP-HorsCampus{
    font-family: "Roboto Thin", sans-serif;
    cursor: pointer;
}

.PLP-questions{
    font-family: "Poiret One", serif;
    color: #13435b;
}

#PLP-surCampusOuNon{
    display: grid;
    grid-template-areas:
            "a a"
            "b c";
    justify-content: center;
    justify-items: center;
    align-items: center;
}

.PLP-Dates-containers{display: grid; justify-content: center}
#PLP-surCampus-question{grid-area: a;}
#PLP-SurCampus{grid-area: b;}
#PLP-horsCampus{grid-area: c;}

.PLP-retours {
    border-style: solid;
    border-color: #13435b;
    border-width: 0.05em;
    margin: 2px;
    font-family: "Roboto Thin", sans-serif;
    width: 80%;
    text-align: center;
}

@media only screen and (min-width: 768px) {
    #ManifestationAssociative-Container{padding-top: 85px;}
    #PLP-SurCampus, #PLP-HorsCampus{font-size: 20px; padding: 10px;}
    .PLP-questions{font-size: 25px; margin: 5px}
    #PLP-surCampusOuNon{grid-gap: 10px; margin: 30px}

    .PLP-retours{font-size: 25px}
    .PLP-Dates-containers{
        grid-template-columns: 500px 200px 200px;
        justify-items: right;
    }
}

@media only screen and (max-width: 767px) {
    #ManifestationAssociative-Container{padding-top: 85px;}
    #PLP-SurCampus, #PLP-HorsCampus{font-size: 20px;}
    .PLP-questions{font-size: 20px}
}